import 'es6-promise/auto';

import loadConfig from '@/utils/config';
import WsGate from '@/wsgate';

import { createBus } from 'suber';
import { h, render } from 'preact';
import TableWidget from '@/components/Table.jsx';


const config = loadConfig('quotesjs')['config'];
let widgets = [];
// holds all symbols of the registered widget instances
let symbolsRegistry = [];
const bus = createBus();

if (!config.hasOwnProperty('instances') || !Array.isArray(config['instances'])) {
    throw new Error('The instances attribute is invalid');
}

// processing and validation of a widget config
config['instances'].forEach((inst) => {
    let config;
    let parent;
    if (!inst.hasOwnProperty('parent') ||
        !(parent = document.querySelector(inst['parent']))
    ) {
        console.debug('Skipped instance. The parent is invalid');
        return;
    }

    if (!inst.hasOwnProperty('config') ||
        !inst['config'].hasOwnProperty('symbols') ||
        !Array.isArray(inst['config']['symbols']) ||
        !inst['config']['symbols'].length === 0
    ) {
        console.debug('Skipped instance. The symbols is invalid');
        return;
    }

    // register the new symbols to the registry
    symbolsRegistry = symbolsRegistry.concat(inst['config']['symbols']);
    inst['parent'] = parent;
    widgets.push(inst);
});

symbolsRegistry = symbolsRegistry.filter((v, i, a) => a.indexOf(v) === i);

function main (widgets) {
    console.time('main_time');

    if (!widgets.length) {
        return;
    }

    let quoteGate = null;
    let gate = new WsGate('wss://ws-gate.fxclub.org/ws-gate/ws', bus);
    gate.open().then((api) => {
        api.subscribe(symbolsRegistry);
        widgets.forEach((w) => {
           mountWidget(w);
        });
        console.timeEnd('main_time');
    });
}

function mountWidget (instance) {
    render((
        <TableWidget bus={bus} {...instance['config']}/>
    ), instance['parent']);
}

main(widgets);



