import { h, render, Component } from 'preact';

import SymbolRow from './table/SymbolRow.jsx';

import { EVENT_QUOTES } from '@/utils/event-bus.js';

export default class Table extends Component {
    constructor (props) {
        super(props);

        let symbols = [];
        (props.symbols || []).forEach(function (symbol) {
            symbols.push({
                code: symbol,
                name: symbol,
                value: 0,
                percentChange: 0
            });
        });

        this.state = {
            symbols: symbols
        };
    }

    updateSymbol (quote) {
        let { symbols } = this.state;
        symbols = symbols.map((symbol) => {
            if (quote.s === symbol.code) {
                symbol.value = quote.r;
            }
            return symbol;
        });

        this.setState({ symbols });
    }

    componentDidMount () {
        this.props.bus.take(EVENT_QUOTES, (q) => {
            this.updateSymbol(q);
        });
    }

    render ({ title }, { loading = true, symbols = [] }) {
        return (
            <div>
                { title }
                <hr />
                { symbols.map(symbol => (
                    <SymbolRow {...symbol}/>
                )) }
            </div>
        );
    }
}



