import { h, render, Component } from 'preact';

export default class SymbolRow extends Component {
    render({ code, name, value, change, percentChange, changeType }) {
        return (
            <div class="symbolrow">
                <div class="symbolrow__name" style="width:170px; margin-right:10px; float:left">{name} ({code})</div>
                <div class="symbolrow__value" style="width:50px; margin-right:10px; float:left">{value}</div>
                 { /*<div class="symbolrow__percent" style="width:50px; margin-right:10px; float:left">{percentChange}%</div> */}
                <div style="clear:both"></div>
            </div>

        );
    }
}


